*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  font-style: none;
  text-decoration: inherit;
  color: inherit;
}
html {
  font-size: 16px;
  font-family: "Supreme", sans-serif;
}
body {
  font-size: 1rem;
}
ul,
li {
  list-style: none;
  display: block;
}
button {
  background-color: none;
  color: inherit;
  border: none;
}
